@font-face {
    font-family: 'Digit';
    src: url('../fonts/DS-DIGIT.TTF') format('truetype');
}

body {
    background-color: #806060;
    width: 100%;
}

div {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

p {
    margin: 30vh;
}

.btns {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    margin-top: 2rem;
}

.btns button {
    width: 8rem;
    height: 2rem;
    border-radius: 10px;
    border: none;
    background-color: rgb(180, 180, 180);
    font-weight: 500;
    font-size: 1.1rem;
    cursor: pointer;
    font-family: 'Roboto', sans-serif;
}

h1 {
    margin-top: 2rem;
}

.timer {
    width: 300px;
    height: 300px;
    background-color: rgba(189, 31, 31, 0.651);
    border-radius: 50%;
    border: 2.5px solid #000;
    font-weight: 900;
    font-size: 3rem;
    font-family: 'Digit';
}

.break {
    width: 300px;
    height: 300px;
    background-color: rgba(37, 57, 184, 0.651);
    border-radius: 50%;
    border: 2.5px solid #000;
    font-weight: 900;
    font-size: 3rem;
    font-family: 'Digit';
}

@media (max-width: 480px) {
    .btns {
        gap: 0.5rem;
        flex-direction: column;
    }

    .timer,
    .break {
        width: 200px;
        height: 200px;
    }
}